@import '@/sass/index.scss';









































































































































.clothing-grid {
  margin: 10% 10% 2% 20%;
  display: flex;
  align-self: left;
}
.text-general-grid {
  padding: 0% 0% 0% 10%;
  text-align: left;
}

h3 {
  text-decoration: underline;
}

h3 span {
  color: #22703A;
  text-decoration: underline;
  text-decoration-color: #22703A;
}
.product-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap: 20px;
  width: auto;
  padding: 0 20px;
  // & /deep/ .product {
  // margin: 0;
  // min-width: 20%;
  // width: auto;
// }
}
